import {DoBootstrap, Injector, NgModule} from '@angular/core';
import {AccessFlowComponent} from './access-flow.component';
import {createCustomElement} from "@angular/elements";
import {CommonModule} from "@angular/common";
import {HttpClientModule} from "@angular/common/http";
import {FormsModule} from "@angular/forms";
import {TranslatePipe} from "./translate.pipe";
import {TranslationService} from "./translation.service";

@NgModule({
  declarations: [
    AccessFlowComponent,
    TranslatePipe
  ],
  imports: [
    CommonModule,
    HttpClientModule,
    FormsModule
  ],
  exports: [
    TranslatePipe
  ],
  providers: [
    TranslationService
  ]
})
export class AccessFlowModule implements DoBootstrap {
  constructor(private injector: Injector) {
    const el = createCustomElement(AccessFlowComponent, { injector: this.injector });
    customElements.define('access-flow', el);
  }

  ngDoBootstrap() {}
}
